.menu-toggle {
  display: inline-flex;
  margin-left: auto;
  padding: 0;
  border: none;
  background-color: transparent;

  @media screen and (min-width: 1280px) {
    display: none;
  }

  @media screen and (min-width: 768px) {
    margin-right: 30px;
  }

  &__icon-menu {
    stroke: var(--bg-dark-btn);

    .menu-toggle:hover &,
    .menu-toggle:focus & {
      stroke: var(--hover-btn-light);
    }
  }
}

.menu-container {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100vh;
  padding-top: 60px;
  background-color: var(--bg-color-mobmenu);
  z-index: 99;

  transform: translateX(100%);
  transition: transform 250ms linear;

  @media screen and (min-width: 768px) {
    width: 40vw;
  }

  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.menu-container.is-open {
  transform: translateX(0);
  box-shadow: -8px 0 20px 0 rgba(0, 0, 0, 0.25);
}

.menu-toggle__icon-cross {
  position: absolute;
  top: 23px;
  right: 34px;
  stroke: var(--bg-light-btn);

  &:hover,
  &:focus {
    stroke: var(--accent-color);
  }
}

//  Button
.menu-btn {
  @extend %btn-light;
  display: block;
  margin-bottom: 32px;
  margin-left: auto;
  margin-right: auto;
  padding: 12px 54px 12px 56px;
  border-radius: 22px;

  &__text {
    margin-right: 22px;
  }

  @media screen and (min-width: 768px) {
    padding-left: 47px;
    padding-right: 45px;

    &__text {
      margin-right: 17px;
    }
  }
}

.mobile-nav__item {
  border-bottom: 1px solid #e18298;

  &:first-child {
    border-top: 1px solid #e18298;
  }
}

.mobile-nav__link {
  display: block;
  padding: 12px 21px;
  width: 100%;
  font-size: 14px;
  line-height: 1.29;
  color: var(--text-white-color);

  &:hover,
  &:focus,
  &--current {
    color: var(--accent-color);
  }
}
